export const displayMap = (locations) => {

  mapboxgl.accessToken =
    "pk.eyJ1IjoiZGhydXZqeW90aXJheSIsImEiOiJjbGc5MHUwZGswejN0M2ZxeW90YW9kaWE4In0.l2im1g-XjebnGJ7N_tmUnw";
  const map = new mapboxgl.Map({
    container: "map", // container ID
    style: "mapbox://styles/dhruvjyotiray/clg99mzh2001901p3adywuj2f", // style URL
    scrollZoom: false,
  });

  const bounds = new mapboxgl.LngLatBounds();

  locations.forEach((loc) => {
    // Create marker
    const el = document.createElement("div");
    el.className = "marker";

    // Add marker
    new mapboxgl.Marker({
      element: el,
      anchor: "bottom",
    })
      .setLngLat(loc.coordinates)
      .addTo(map);

    // Add popup
    new mapboxgl.Popup({
      offset: 30,
    })
      .setLngLat(loc.coordinates)
      .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
      .addTo(map);

    // Extend map bounds to include current location
    bounds.extend(loc.coordinates);
  });

  map.fitBounds(bounds, {
    padding: {
      top: 200,
      bottom: 150,
      left: 100,
      right: 100,
    },
  });
};
